import React from "react";
import { styleHeaderBanner } from "./Elements/HeaderConstant";
import { uploadedUrl } from "../../../constants";
const HeaderType4 = ({ isDisplay, Banner }) => {
  const { bannerStyle, subTitleStyle, titleStyle, iconAreaStyle, iconStyle, isFacebook, isTwitter, isInstagram, facebookIconAreaStyle, facebookIconStyle, twitterIconAreaStyle, twitterIconStyle, instagramIconAreaStyle, instagramIconStyle, subTitleVarentClass, titleVarentClass } = styleHeaderBanner(Banner);

  var bCustomStyle = bannerStyle;
  if (bCustomStyle && bCustomStyle.backgroundImage.includes("bg-bnr-3.png") && bCustomStyle.backgroundImage.includes(uploadedUrl)) bCustomStyle.backgroundImage = bCustomStyle.backgroundImage.replace(uploadedUrl, "https://test.taggbox.com/widget/assets/media/banner/")
  return <div className="tb_wall_header_wrap__" style={bCustomStyle}>
    <div className="tb_header4_row__">
      <div className="tb_header4_column_content__">
        <div className="tb_header4_content__">
          <div className={`tb_header4_subtitle__ tb-cTBfont-${subTitleVarentClass}`} style={subTitleStyle}>{Banner.subTitle}</div>
          <div className={`tb_header4_title__ tb-cTBfont-${titleVarentClass}`} style={titleStyle}>{Banner.title}</div>
        </div>
      </div>
      <div className="tb_header4_column_icons__">
        {Banner.social_icon_status == 1 ? <div className="tb_header4_social_icons__">
          {isFacebook ?
            <a onClick={() => !isDisplay ? Banner.facebook_url && window.open(Banner.facebook_url, "_blank") : null }  className="tb_header4_social_icon_list__" aria-label="Facebook" style={{ ...iconAreaStyle, ...facebookIconAreaStyle }}>
              <div className={`tb_header4_social_icon__ tb__icon tb-facebook`} style={{ ...iconStyle, ...facebookIconStyle }}> </div>
            </a> : ''}
          {isTwitter ?
            <a onClick={() => !isDisplay ? Banner.twitter_url && window.open(Banner.twitter_url, "_blank") : null }  className="tb_header4_social_icon_list__" aria-label="Twitter" style={{ ...iconAreaStyle, ...twitterIconAreaStyle }}>
              <div className="tb_header4_social_icon__ tb__icon tb-twitter" style={{ ...iconStyle, ...twitterIconStyle }}></div>
            </a> : ''}
          {isInstagram ?
            <a onClick={() => !isDisplay ? Banner.instagram_url && window.open(Banner.instagram_url, "_blank") : null } className="tb_header4_social_icon_list__" aria-label="Instagram" style={{ ...iconAreaStyle, ...instagramIconAreaStyle }}>
              <div className="tb_header4_social_icon__ tb__icon tb-instagram" style={{ ...iconStyle, ...instagramIconStyle }}> </div>
            </a> : ''}
        </div>
          : null}</div>
    </div>
  </div>
}
export default HeaderType4